<template>
  <el-popover placement="right" :width="10" trigger="click">
    <template #reference
      ><!--reference	触发 Popover 显示的 HTML 元素-->
      <el-button style="margin-right: 16px">添加</el-button>
    </template>

    名称:<br /><el-input v-model="name"></el-input><br />
    网址:<br /><el-input></el-input><br />
    图标（选填）:<br /><el-input></el-input><br />
    <el-button @click="create">确定</el-button><el-button>取消</el-button>
  </el-popover>
</template>
<script>
export default {
  methods: {
    create() {
      // childByValue是在父组件on监听的方法
      // 第二个参数this.childValue是需要传的值
      this.$emit('childByValue', this.childValue)
    },
  },
}
</script>
