<template>
  <div @mouseover="hover = true" @mouseleave="hover = false">
    <a
      :href="href"
      target="_blank"
      style="text-decoration: none; font-color: red"
    >
      <el-container>
        <el-header><img class="icon" :src="icon" /></el-header>
        <el-main
          ><h style="color: #167592; font-size: 16px">{{ appName }}</h></el-main
        >
      </el-container>
    </a>
  </div>
</template>

<style scoped>
.icon {
  width: 50px;
  margin-top: 20px;

}

div {
  width: 200px;
  height: auto;
  margin: 0px;
  padding: 0px;
  padding-bottom: 20px;
}
div:hover {
  transform: scale(1.25);
}
</style>

<script>
export default {
  props: ['icon', 'appName', 'href'],

  data() {
    return {
      hover: false,
    }
  },
}
</script>
