<template>
  <div style="width: 95%; height: 50px;display: flex;align-items: center;
  justify-content: center;">
    <el-dropdown>
      <div style="padding:0px,margin:auto;">
        <img src="https://www.baidu.com/favicon.ico" />
      </div>
      <template #dropdown>
        <el-dropdown-menu>
          <el-dropdown-item @click.native="baidu">百度</el-dropdown-item>
          <el-dropdown-item @click.native="sougou">搜狗</el-dropdown-item>
          <el-dropdown-item>谷歌</el-dropdown-item>
        </el-dropdown-menu>
      </template>
    </el-dropdown>
    <input
      @keyup.enter="search"
      @input="$forceUpdate()"
      v-model="input"
      autofocus
    />
    <button @click.native="search">搜索</button>
  </div>
</template>
<style scoped>
input {
  background-color: transparent;
  border: solid 1px pink;
  border-radius: 17px;
  outline: none;
  width: 75%;
  height: 50px;
  margin: 10px;
  font-size: 20px;
  color: bisque;
  padding-left: 20px;
}

input.text {
  color: aqua;
}
img {
  width: 45px;
  height: 45px;
  border-radius: 30px;
}
button {
  width: 5%;
  height: 50px;
  background-color: transparent;
  border: solid 1px pink;
  border-radius: 17px;
  outline: none;
  font-size: 100%;
  color: bisque;
}
</style>
<script>
import { ref } from 'vue'
const input = ''
let s = 1
export default {
  methods: {
    baidu() {
      return (this.s = 1)
    },
    sougou() {
      return (this.s = 2)
    },
    search() {
      let href = ''
      if (s == 1) href = 'https://www.baidu.com/s?wd=' + this.input
      else if (s == 2) href = 'https://www.sogou.com/web?query=' + this.input
      window.open(href, '_blank')
    },
  },
}
</script>
